.container{
    width: 40%;
    margin: auto;
    height: 75%;
    background: white;
    color: #5f205fce;
    gap: 20px;
    padding: 10px 30px;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}

.container ul li {
    display: flex;
    align-items: center;
    height: 30px;
    padding: 0px 15px;
    border: 1px solid #5f205fce;
    border-radius: 10px;
    margin-bottom: 20px;
    font-size: 15px;

}

.container button{
    color: white;
    background: #5f205f;
    width: 120px;
    height: 40px;
    font-size: 15px;
    border-radius: 10px;
    margin: auto;
}

.container .index{
    text-align: center;
}

.container .correct{
    border-color: cyan ;
    background: cyan;
}

.container .wrong{
    border-color: #ff2a2a;
    background: #ff2a2a;
}